    min-width: 780px; }
  .App-header {
    width: 100vw;
    font-size: .8rem; }
    .App-header nav {
      font-size: 1rem; } }

nav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  font-size: 1.6rem;
  font-weight: 600;
  min-width: 480px; }

nav li {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  height: 100%;
